<style>

</style>

<template>
  <div>
    waiting for loading...
    <span>剩余</span>
    <span v-if="remainingTime.day !== undefined" class="date-font">{{ zeroPadding(remainingTime.day) }}</span>天，
    <span v-if="remainingTime.hour !== undefined" class="date-font">{{ zeroPadding(remainingTime.hour) }} ：</span>
    <span v-if="remainingTime.minute !== undefined" class="date-font">{{ zeroPadding(remainingTime.minute) }} ：</span>
    <span v-if="remainingTime.second !== undefined" class="date-font">{{ zeroPadding(remainingTime.second) }}</span>
  </div>
</template>

<script>
  const CURRENT_DATE = '2018-05-06';
  export default {
    data() {
      return {
        remainingTime: {
          day: undefined,
          hour: undefined,
          minute: undefined,
          second: undefined,
        }
      };
    },
    components: {

    },
    methods: {
      initData: function () {
        if(this.checkDate()) {
          this.$router.push('welcome');
        }
        this.timerID = setInterval(this.updateTime, 1000);
      },
      updateTime: function () {
        let nS = new Date(CURRENT_DATE + ' 00:00:00') - Date.now();
        this.calculateNS(nS);
      },
      checkDate: function() {
        let now = Date.now();
        let start = new Date(CURRENT_DATE + ' 00:00:00');
        let end = new Date(CURRENT_DATE + ' 23:59:59');
        return (now >= start && now <=end);
      },
      calculateNS: function(nS) {
        debugger;
        if(nS <= 999) {
          this.$router.push('welcome');
        }
        nS /= 1000;
        nS = parseInt(nS.toString());
        this.remainingTime.second = nS % 60;
        nS = (nS - this.remainingTime.second) / 60;
        if(nS === 0) {
          this.remainingTime.minute = 0;
          this.remainingTime.hour = 0;
          return;
        }
        this.remainingTime.minute = nS % 60;
        nS = (nS - this.remainingTime.minute) / 60;
        if(nS === 0) {
          this.remainingTime.hour = 0;
          return;
        }
        this.remainingTime.hour = nS % 24;
        nS = (nS - this.remainingTime.hour) / 24;
        if(nS === 0) {
          return;
        }
        this.remainingTime.day = nS;
      },
      zeroPadding: function (num) {
        let zero = '', digit = 2;
        for (let i = 0; i < digit; i++) {
          zero += '0';
        }
        return (zero + num).slice(-digit);
      },
    },
    created() {
    },
    beforeDestory() {
    },
    destoryed() {
    },
    mounted() {
      this.initData();
    },
    watch: {
    },
    beforeRouteEnter(to, from, next) {
      next();
    },
    beforeRouteUpdate(to, from, next) {
      next();
    },
    beforeRouteLeave(to, from, next) {
      // 清除实时时钟
      try {
        if(this.timerID !== undefined) {
          clearInterval(this.timerID);
        }
      } catch (e) {
        console.log("error, " + e);
      } finally {
        next();
      }
    }

  }
</script>



